import React, {Component} from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";

import OnlineUtils from "eshop/utils/OnlineUtils";
import {changeAddTerminalToOfferBundleNo, fetchMiniCart} from "eshop/modules/cart/actions/cart";
import OfferType from "../../constants/OfferTypeEnum";
import {setOfferType, setOfferTypeCmsConf} from "eshop/modules/simfree/actions/app";
import {
    getOfferTypesForSelect,
    getProductListHeader,
    isProductDetailsPage,
    isProductListPage,
    isAccessoryListPage, getSelectedSoftBundleGroup,
} from "eshop/modules/simfree/selectors";

import {
    getMarketContext,
    getPropositionListCount,
    getPropositionListCountSetMode,
    getPropositionListOfferType,
    getSelectedOfferType,
    getUseDefaultOfferType,
} from "eshop/modules/configurator/selectors/filters";
import {USE_DEFAULT_OFFER} from "eshop/modules/configurator/actionTypes"

import {subscribeCustomerSelected} from "../../../checkout/actions/data";
import ProductTabHeaderView from "./view/ProductTabHeaderView";
import ProductListHeaderView from "./view/ProductListHeaderView";
import ProductSwitcherHeaderView from "./view/ProductSwitcherHeaderView";
import ProductChangingHeaderView from "./view/ProductChangingHeaderView";
import {getAddTerminalToOfferBundleNo} from "eshop/modules/cart/selectors";
import {redirectToCart} from "eshop/modules/configurator/actions/cart";
import OraLoyaltyHeader from "../../../magnum2/components/wwt/OraLoyaltyHeader";
import {changeCurrentPageProps, setAllVisibilityProduct} from "eshop/modules/simfree/actions/filter";
import {getProductListData, getProductListWrapper} from "../../selectors";
import { setSalesChannel } from "eshop/modules/auth/actions/authorization";
import {getStayLoveRetentionMSISDN, hasLove, hasODF} from "../../../auth/selectors/authorization";
import {getMagnumType} from "eshop/modules/magnum2/selectors";
import StayLoveMsisdnHeader from "eshop/modules/cart/components/entriesList/StayLoveMsisdnHeader";
import {hasVoiceCartEntry} from "../../../cart/selectors";
import {isDuet} from "../../selectors";
import OraVasPacketPickerComponent from "eshop/modules/simfree/components/modals/OraVasPacketPickerComponent";

class ProductListHeaderComponent extends Component {

    constructor(props) {
        super(props);
    }

    componentWillMount() {
        const cmsConf = this.props.cmsConf;
        if(!this.props.isProductDetailsPage && !this.props.isProductListPage && !this.props.isAccessoryListPage) {
            this.props.dontUseDefaultOffer();
        }
        this.props.registerCmsConfiguration(cmsConf, this.props.validOfferTypes);
        this.props.changeAddTerminalToOfferBundleNo(null);
        this.selectOfferType = this.selectOfferType.bind(this);
        this.props.fetchMiniCart();
    }

    componentDidMount() {
        this.props.setSalesChannel(this.props.channels.sales);
        if (this.props.channels.sales !== "WWW") {
            this.props.subscribeCustomerSelected();
        }

        if (this.props.offerType && this.props.useDefaultOfferType) {
            this.selectOfferType(this.props.offerType);
        }

    }

    selectOfferType(value) {
        if (document.getElementById("expander-module-one-time")) {
            OPL.UI.stopModules(document.getElementById("expander-module-one-time"));
            OPL.UI.initModules(document.getElementById("expander-module-one-time"));
        }
        if (document.getElementById("expander-module-monthly")) {
            OPL.UI.stopModules(document.getElementById("expander-module-monthly"));
            OPL.UI.initModules(document.getElementById("expander-module-monthly"));
        }

        //for defaults to work
        OnlineUtils.removeFromSessionStorage("selectedPropositionId");
        OnlineUtils.removeFromSessionStorage("selectedPropositionPosition");
        OnlineUtils.removeFromSessionStorage("processType");
        OnlineUtils.changeOrAddUrlParameter("processType", null);
        OnlineUtils.changeOrAddUrlParameter("serviceplan", null);

        this.props.setOfferType(value);
        this.props.setAllVisibility(false);
        this.props.changeCurrentPage(1);

    }

    getChangeTerminalLabel = (cmsConf, selectedOfferType) => {
        if (cmsConf
            && cmsConf[selectedOfferType]
            && cmsConf[selectedOfferType].cmsDescriptions
            && cmsConf[selectedOfferType].cmsDescriptions["changeTerminalLabel"])
            return cmsConf[selectedOfferType].cmsDescriptions["changeTerminalLabel"];
        else
            return "ZmieÅ„ terminal";
    };

    getBundleGroupsProps = ({hasLove = false, hasODF = false, softBundleGroup = null}) => ({
        hasLove, hasODF, softBundleGroup,
    });
    filterVisible(options){
        const {hasVoiceCartEntry,stayLoveRetentionMSISDN, hasLove, cmsConf} = this.props;

        if(!!cmsConf["VOICE_LDF"] && ((hasLove && hasVoiceCartEntry) || stayLoveRetentionMSISDN)){
            cmsConf["VOICE_LDF"].visible=true;
        }
        return options.filter(option=> cmsConf[option.value] && cmsConf[option.value].visible);

    }
    render() {
        const {wwt} = this.props;
        let headerView;
        console.log("ProductListHeaderComponent render", this.props);

        //For LP while setting sim count
        if ((this.props.isPropositionListCountSetMode || this.props.propositionListCount === 0) && !(this.props.isProductListPage || this.props.isProductDetailsPage)) {
            return (<div id={"offerTypeSelectionComponent"}/>);
        }
        //For duets - no worlds change during sim count loop
        if (!this.props.isB2B && (this.props.propositionListCount > 0 && this.props.isPropositionListCountLoop) && (this.props.isProductListPage || this.props.isProductDetailsPage)) {
            return (<div id={"offerTypeSelectionComponent"}><StayLoveMsisdnHeader/></div>);
        }
        if (this.props.addTerminalToOfferBundleNo) {
            return (
                <ProductChangingHeaderView
                    redirectToCart={this.props.redirectToCart}
                    descriptions={this.props.descriptions}
                    changeTerminalLbl={this.getChangeTerminalLabel(this.props.cmsConf, this.props.selectedOfferType)}
                    channels={this.props.channels}
                />
            )
        }

        let options = this.props.selectedOfferType !== OfferType.CONVERGENT ? this.props.selectValues.filter(o => OfferType.CONVERGENT !== o.value) :
            this.props.selectValues.filter(t => this.props.magnumType === t.value);
        if (this.props.selectionType === "HIDDEN") {
            headerView = null;
        }
        options=this.filterVisible(options);

        if (this.props.selectionType === "DROPDOWN") {
            headerView = (
                <ProductListHeaderView
                    headerText={this.props.headerText || "telefony"}
                    onChange={this.selectOfferType}
                    options={options}
                    descriptions={this.props.descriptions}
                    channels={this.props.channels}
                />
            )
        }
        if (this.props.selectionType === "TAB") {
            headerView = (
                <ProductTabHeaderView
                    headerText={this.props.headerText || "telefony"}
                    onChange={this.selectOfferType}
                    options={options}
                    descriptions={this.props.descriptions}
                    tabHeaderText={this.props.tabHeaderText}
                    channels={this.props.channels}
                    selectedOfferType={this.props.selectedOfferType}
                    isDuet={this.props.isDuet}
                    {...this.getBundleGroupsProps(this.props)}
                />
            )
        }
        if (this.props.selectionType === "SWITCHER") {
            headerView = (
                <ProductSwitcherHeaderView
                    headerText={this.props.headerText}
                    onChange={this.selectOfferType}
                    options={options}
                    descriptions={this.props.descriptions}
                    tabHeaderText={this.props.tabHeaderText}
                    channels={this.props.channels}
                    selectedOfferType={this.props.selectedOfferType}
                />
            )
        }
        if (this.props.selectedOfferType === OfferType.CONVERGENT) {
            const descriptions = {};
            const isAddressComplete = !!wwt.zip;
            return (
                <div>
                    <div className="u-box-shadow" >
                        {headerView}
                    </div>

                    <div className={"l-page-row" + (isAddressComplete ? "" : " u-hide")}>
                        <OraLoyaltyHeader
                            place={wwt.cityName}
                            street={wwt.streetName}
                            streetNumber={wwt.streetNumber}
                            apartmentNumber={wwt.apartmentNumber}
                            onClickChange={null}
                            descriptions={descriptions}

                        />
                    </div>
                </div>
            );
        }
        return (
            <div>
                    <div id={"offerTypeSelectionComponent"}>{headerView}</div>
                    <StayLoveMsisdnHeader/>
                    <OraVasPacketPickerComponent/>
            </div>
        );
    }
}

ProductListHeaderComponent.propTypes = {
    cmsConf: PropTypes.object,
    selectionType: PropTypes.string,
    descriptions: PropTypes.object,
    headerText: PropTypes.string,
    tabHeaderText: PropTypes.string,
    selectValues: PropTypes.any,
    selectedOfferType: PropTypes.string,
    wwt: PropTypes.object,
    registerCmsConfiguration: PropTypes.func,
    setOfferType: PropTypes.func,
    subscribeCustomerSelected: PropTypes.func,
    changeAddTerminalToOfferBundleNo: PropTypes.func,
    redirectToCart: PropTypes.func,
    hasLove: PropTypes.bool,
    hasODF: PropTypes.bool,
    softBundleGroup: PropTypes.string,
    channels: PropTypes.object,
    hasVoiceCartEntry: PropTypes.bool,
    stayLoveRetentionMSISDN: PropTypes.string,
};

const mapDispatchToProps = dispatch => ({
    registerCmsConfiguration: (cmsConf, validOfferTypes) => dispatch(setOfferTypeCmsConf(cmsConf, validOfferTypes)),
    setOfferType: offerType => dispatch(setOfferType(offerType)),
    subscribeCustomerSelected: () => dispatch(subscribeCustomerSelected()),
    changeAddTerminalToOfferBundleNo: () => dispatch(changeAddTerminalToOfferBundleNo(null)),
    redirectToCart: () => dispatch(redirectToCart()),
    fetchMiniCart: () => dispatch(fetchMiniCart()),
    changeCurrentPage: page => dispatch(changeCurrentPageProps(page)),
    setAllVisibility: value => dispatch(setAllVisibilityProduct(value)),
    dontUseDefaultOffer: ()=> dispatch({type: USE_DEFAULT_OFFER, use: false}),
    setSalesChannel: channel => dispatch(setSalesChannel(channel)),
});

const mapStateToProps = state => ({
    useDefaultOfferType: getUseDefaultOfferType(state),
    isPropositionListCountSetMode: getPropositionListCountSetMode(state),
    propositionListCount: getPropositionListCount(state),
    headerText: getProductListHeader(state),
    selectValues: getOfferTypesForSelect(state),
    selectedOfferType: getSelectedOfferType(state),
    wwt: state.magnum.wwt,
    addTerminalToOfferBundleNo: getAddTerminalToOfferBundleNo(state),
    products: getProductListWrapper(state),
    productList: getProductListData(state),
    isProductListPage: isProductListPage(state),
    isAccessoryListPage: isAccessoryListPage(state),
    isProductDetailsPage: isProductDetailsPage(state),
    isPropositionListCountLoop: getPropositionListOfferType(state) && getPropositionListOfferType(state) == getSelectedOfferType(state),
    isB2B: getMarketContext(state) === "B2B",
    hasLove: hasLove(state),
    hasODF: hasODF(state),
    softBundleGroup: getSelectedSoftBundleGroup(state),
    magnumType: getMagnumType(state),
    stayLoveRetentionMSISDN: getStayLoveRetentionMSISDN(state),
    hasVoiceCartEntry: hasVoiceCartEntry(state),
    isDuet: isDuet(state),
});

export default connect(mapStateToProps, mapDispatchToProps)(ProductListHeaderComponent);